<template>
  <div class="contain_wrap">
    <div class="return" @click="returnGo">
      <van-icon name="arrow-left" />
    </div>
    <div class="search_wrapp">
      <div class="inp_wrap">
        <van-search
          v-model="inpText"
          show-action
          placeholder="请输入搜索关键词"
          @input="onSearch(inpText)"
          @cancel="onCancel"
        />
      </div>
    </div>
    <div class="search_contain">
      <router-link
        class="search_item"
        :to="'/goodsdetail?goods_id='+item.goods_id"
        v-for="(item,index) in searchData"
        :key="index"
      >{{item.goods_name}}</router-link>
    </div>
  </div>
</template>

<script>
import { reaxios } from "@/assets/js/reaxios";
import "@/assets/css/category.css";
export default {
  data() {
    return {
      inpText: "",
      searchData: []
    };
  },

  created() {},

  methods: {
    returnGo() {
      this.$router.go(-1);
    },
    onSearch(val) {
      reaxios(0, { url: "/goods/qsearch", params: { query: val } }).then(
        result => {
          this.searchData = result.data.message;
        }
      );
    },
    onCancel() {
      this.$toast("取消");
    }
  }
};
</script>

<style lang="less" scoped>
.contain_wrap {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgb(243, 243, 243);
  padding: 10px;
  .return {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 95%;
    background-color: var(--themeColor);
    text-align: left;
    font-size: 24px;
    display: flex;
    align-items: center;
    color: aliceblue;
  }

  .search_wrapp {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 30px;
    .van-search {
      background-color: rgba(0, 0, 0, 0);
    }
    .inp_wrap {
      flex: 6;
      padding: 4px;
    }
  }
  .search_contain {
    position: fixed;
    top: 100px;
    bottom: 20px;
    overflow: scroll;
    .search_item {
      background-color: #fff;
      margin: 5px 10px;
      padding: 5px;
      display: -webkit-box;
      overflow: hidden;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      text-align: left;
      color: #000;
    }
  }
}
</style>


